<template>
  <div class="container" v-loading="loading">
    <el-card class="box-card userscore" v-if="scorelist.length>0">
      <div slot="header" class="clearfix">
        <span>Socres</span>
      </div>
      <div class="item" v-for="(item,index) in scorelist">{{index+1}}.{{item.text}}:{{item.score}}</div>
    </el-card>
    <studymask :time="4" v-model="maskloading" v-if="maskloading" @end="maskend" @leftone="leftone"></studymask>
    <el-page-header @back="goBack" content="Lesson Demo:Bear Family"></el-page-header>
    <el-row :gutter="20" style="margin-top: 20px;">
      <el-col :lg="17">
        <el-image style="width: 100%; height: 100%" :src="pictureurl" fit="fill"></el-image>
      </el-col>
      <el-col :lg="7">
        <div style="flex: 1;border: 2px solid #222;">
          <video-player class="video-player vjs-custom-skin" ref="videoPlayer2" :playsinline="true" :options="playerOptions1"
            @timeupdate="onPlayerTimeupdate($event)" @ended="videoended"></video-player>
        </div><!-- @timeupdate="onPlayerTimeupdate($event)" @ended="videoended" -->
        <div style="flex: 1;margin-top: 30px;text-align: center;">
          <camera ref="camera1"></camera>
        </div>
      </el-col>
    </el-row>
    <el-row :gutter="20" class="el-row-item">
      <el-col :lg="4">
        <el-image src="/static/picture/Polarbearfamily.jpg"></el-image>
      </el-col>
      <el-col :lg="20">
        <div class="title">Lesson Demo:Bear Family</div>
        <div class="desc">bear family</div>
        <div class="btn"></div>
        <div class="rate">
          <el-rate v-model="value" disabled show-score text-color="#ff9900" score-template="{value}"></el-rate>
        </div>
      </el-col>
      <el-col style="margin-top: 20px;">
        <el-tabs type="border-card">
          <el-tab-pane>
            <span slot="label"><i class="el-icon-postcard"></i> Introduction</span>
            <div class="content">
              <p>[In the follow me scenario, results are in 3 categories: correct, mediocre, wrong/no repeat; if
                correct, teacher says EXCELLENT, if mediocre teacher pronounce again with slower speed, if wrong/no
                repeat, teacher say LETS TRY ONE MORE TIME and pronounce again. If after teacher repeat, student still
                doesnt get it, then teacher moves on and tell student WE WILL ADDRESS THIS LATER]</p>

            </div>
          </el-tab-pane>
          <el-tab-pane label="">
            <span slot="label"><i class="el-icon-user-solid"></i> Teacher</span>
            <div style="text-align: center;">
              <el-avatar :size="80" src="/static/images/teacher.jpg"></el-avatar>
              <h3>Mr Mike</h3>
              <el-tag type="success">professor</el-tag>
              <div class="content" style="text-align: left;">
                <p>In the describe scenario, results are in 3 categories: correct, mediocre, wrong/no describe; if
                  correct, teacher says EXCELLENT, if mediocre teacher pronounce again with slower speed, if wrong/no
                  repeat, teacher say LETS TRY ONE MORE TIME and pronounce again. If after teacher repeat, student
                  still doesnt get it, then teacher moves on and tell student WE WILL ADDRESS THIS LATER</p>
              </div>
            </div>
          </el-tab-pane>
          <el-tab-pane>
            <span slot="label"><i class="el-icon-guide"></i> Catalogue</span>
            <el-timeline>
              <el-timeline-item v-for="(activity, index) in activities" :key="index" :icon="activity.icon" :type="activity.type"
                :color="activity.color" :size="activity.size" :timestamp="activity.timestamp">
                {{activity.content}}
              </el-timeline-item>
            </el-timeline>
          </el-tab-pane>
        </el-tabs>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  import StudyController from '@/api/StudyController'
  import camera from '@/web/components/camera'
  import studymask from '@/web/components/mask'
  import eventBus from '@/utils/eventBus.js'
  import HZRecorder from '@/api/HZRecorder.js'
  export default {
    name: 'study',
    components: {
      camera,
      studymask
    },
    data() {
      return {
        username: 'jack',
        maskloading: false,
        title: 'Online Study',
        loading: true,
        value: 4.5,
        studystep: 1,
        scorelist: [],
        pictureurl: '/static/picture/Polarbearfamily.jpg',
        followIndex: 0,
        followRepeat: 0,
        followText: ['Bear', 'Its\na\nbear', 'Baby\nbear', 'Mom\nbear', 'They\nare\nbear\nfamily', 'Black\nbear',
          'Baby\nblack\nbear', 'Mom\nblack\nbear', 'They\nare\nblack\nbear\nfamily', 'Mom\nbear', 'Baby\nbear',
          'They\nare\nbear\nfamily', 'Mom\nblack\nbear', 'Baby\nblack\nbear', 'They\nare\nblack\nbear\nfamily'
        ],
        playerOptions1: {
          // 
          playbackRates: [0.5, 1.0, 1.5, 2.0],
          // true,
          autoplay: false,
          // 
          muted: false,
          // 
          loop: false,
          // <video>auto,
          preload: 'auto',
          language: 'zh-CN',
          //  - "16:9""4:3"
          aspectRatio: '16:9',
          // trueVideo.js player
          fluid: true,
          sources: [{
            // 
            type: 'video/mp4',
            // url
            src: '/static/video/1_6.mp4'
          }],
          // 
          poster: '',
          // Video.js
          notSupportedMessage: '',
          controlBar: false
        },
        activities: [{
          content: '1.Baby bear',
          timestamp: '00:15',
          size: 'large',
          type: 'primary'
        }, {
          content: '2.Mom bear',
          timestamp: '10:46',
          color: '#0bbd87'
        }, {
          content: '3.They are bear family',
          timestamp: '20:46',
          size: 'large'
        }, {
          content: '4.Baby black bear',
          timestamp: '30:46'
        }]
      }
    },
    mounted() {
      var self = this
      self.username = self.$route.params.username
      this.getData()
      StudyController.playAudio("hi," + self.username + "let's start", function() {
        self.player2.play()
      })
    },
    destroyed() {
      StudyController.stop()
      if (self.recorder) {
        self.recorder.close()
      }
    },
    computed: {
      player2() {
        return this.$refs.videoPlayer2.player
      }
    },
    methods: {
      maskend: function() {

      },
      getData: function() {
        this.loading = false
      },
      goBack: function() {
        this.$router.push('/entrance')
      },
      closeCamera: function() {
        this.$refs.camera1.closeCamera()
      },
      onPlayerTimeupdate: function(player) {
        var self = this
        var currentTime = player.currentTime()
        if (this.studystep === 1) {
          if (currentTime >= 0 && currentTime < 10) {
            self.changePic('Babypolarbear')
          } else if (currentTime >= 10 && currentTime < 13) {
            self.changePic('Babypolarbear')
          } else if (currentTime >= 13 && currentTime < 16) {
            self.changePic('Mompolarbear')
          } else if (currentTime >= 16) {
            self.changePic('Polarbearfamily')
          }
        }
        if (this.studystep === 12) {
          if (currentTime >= 0 && currentTime < 3) {
            self.changePic('Babyblackbear')
          } else if (currentTime >= 3 && currentTime < 6) {
            self.changePic('Babyblackbear')
          } else if (currentTime >= 6 && currentTime < 10) {
            self.changePic('Momblackbear')
          } else if (currentTime >= 10) {
            self.changePic('Blackbearfamily')
          }
        }
      },
      changePic: function(pictureName) {
        if (this.pictureurl !== '/static/picture/' + pictureName + '.jpg') {
          this.pictureurl = '/static/picture/' + pictureName + '.jpg'
        }
      },
      videoended: function() {
        var self = this
        if (this.studystep === 1) {
          this.studystep++
          self.changePic('Babypolarbear')
          this.player2.src('/static/video/7.mp4')
          this.player2.play()
        } else if (this.studystep === 2) {
          this.followIndex = 0
          this.followRead()
        } else if (this.studystep === 3) {
          this.studystep++
          self.changePic('Babypolarbear')
          this.player2.src('/static/video/7.mp4')
          this.player2.play()
        } else if (this.studystep === 4) {
          if (this.followRepeat === 0) {
            this.followIndex++
          }
          this.followRead()
        } else if (this.studystep === 5) {
          this.studystep++
          self.changePic('Babypolarbear')
          this.player2.src('/static/video/9.mp4')
          this.player2.play()
        } else if (this.studystep === 6) {
          if (this.followRepeat === 0) {
            this.followIndex++
          }
          this.followRead()
        } else if (this.studystep === 7) {
          this.studystep++
          self.changePic('Mompolarbear')
          this.player2.src('/static/video/10.mp4')
          this.player2.play()
        } else if (this.studystep === 8) {
          if (this.followRepeat === 0) {
            this.followIndex++
          }
          this.followRead()
        } else if (this.studystep === 9) {
          this.studystep++
          self.changePic('Polarbearfamily')
          this.player2.src('/static/video/11.mp4')
          this.player2.play()
        } else if (this.studystep === 10) {
          if (this.followRepeat === 0) {
            this.followIndex++
          }
          this.followRead()
        } else if (this.studystep === 11) {
          this.studystep++
          this.player2.src('/static/video/12-15.mp4')
          this.player2.play()
        } else if (this.studystep === 12) {
          this.studystep++
          self.changePic('Babyblackbear')
          this.player2.src('/static/video/16.mp4')
          this.player2.play()
        } else if (this.studystep === 13) {
          if (this.followRepeat === 0) {
            this.followIndex++
          }
          this.followRead()
        } else if (this.studystep === 14) {
          this.studystep++
          self.changePic('Babyblackbear')
          this.player2.src('/static/video/17.mp4')
          this.player2.play()
        } else if (this.studystep === 15) {
          if (this.followRepeat === 0) {
            this.followIndex++
          }
          this.followRead()
        } else if (this.studystep === 16) {
          this.studystep++
          self.changePic('Momblackbear')
          this.player2.src('/static/video/18.mp4')
          this.player2.play()
        } else if (this.studystep === 17) {
          this.followIndex++
          this.followRead()
        } else if (this.studystep === 18) {
          this.studystep++
          self.changePic('Blackbearfamily')
          this.player2.src('/static/video/19.mp4')
          this.player2.play()
        } else if (this.studystep === 19) {
          if (this.followRepeat === 0) {
            this.followIndex++
          }
          this.followRead()
        } else if (this.studystep === 20) {
          this.studystep++
          self.changePic('Mompolarbear')
          this.player2.src('/static/video/20-25.mp4')
          this.player2.play()
        } else if (this.studystep === 21) {
          if (this.followRepeat === 0) {
            this.followIndex++
          }
          this.followRead()
        } else if (this.studystep === 22) {
          this.studystep++
          self.changePic('Babypolarbear')
          this.player2.src('/static/video/20-25.mp4')
          this.player2.play()
        } else if (this.studystep === 23) {
          if (this.followRepeat === 0) {
            this.followIndex++
          }
          this.followRead()
        } else if (this.studystep === 24) {
          this.studystep++
          self.changePic('Polarbearfamily')
          this.player2.src('/static/video/20-25.mp4')
          this.player2.play()
        } else if (this.studystep === 25) {
          if (this.followRepeat === 0) {
            this.followIndex++
          }
          this.followRead()
        } else if (this.studystep === 26) {
          this.studystep++
          self.changePic('Babyblackbear')
          this.player2.src('/static/video/20-25.mp4')
          this.player2.play()
        } else if (this.studystep === 27) {
          if (this.followRepeat === 0) {
            this.followIndex++
          }
          this.followRead()
        } else if (this.studystep === 28) {
          this.studystep++
          self.changePic('Momblackbear')
          this.player2.src('/static/video/20-25.mp4')
          this.player2.play()
        } else if (this.studystep === 29) {
          if (this.followRepeat === 0) {
            this.followIndex++
          }
          this.followRead()
        } else if (this.studystep === 30) {
          this.studystep++
          self.changePic('Blackbearfamily')
          this.player2.src('/static/video/20-25.mp4')
          this.player2.play()
        } else if (this.studystep === 31) {
          if (this.followRepeat === 0) {
            this.followIndex++
          }
          this.followRead()
        } else if (this.studystep === 32) {
          StudyController.playAudio(self.username + "that's it,bye", function() {
            // self.$refs.camera1.closeCamera()
            self.$alert('go to lessons', 'class is over', {
              confirmButtonText: 'ok',
              callback: action => {
                self.$router.push({
                  path: '/entrance'
                })
              }
            })
          })
        }
      },
      followRead: function() {
        var self = this
        self.maskloading = true
        HZRecorder.get(function(rec) {
          self.recorder = rec
          self.recorder.start()
        })
      },
      leftone: function() {
        var self = this
        self.recorder.stop()
        self.followRepeat++
        let uploadurl = process.env.API_HOST + '/ise'
        self.recorder.upload('[word]\n' + self.followText[self.followIndex], 'read_word', uploadurl, function(state,
          e) {
          switch (state) {
            case 'uploading':
              break
            case 'ok':
              self.followend(e)
              break
            case 'error':
              self.followend(500)
              break
            case 'cancel':
              self.followend(500)
              break
          }
        })
      },
      followend: function(e) {
        let self = this
        let readscore = 0
        if (e === 500) {
          readscore = 0
        } else {
          let response = e
          readscore = response.data.read_word.rec_paper.read_word.total_score.split('.')[0]
        }
        self.$notify({
          type: 'error',
          title: 'Score',
          dangerouslyUseHTMLString: true,
          message: readscore
        })
        if (readscore >= 90) {
          self.scorelist.push({
            text: self.followText[self.followIndex],
            score: readscore
          })
          self.followRepeat = 0
          self.studystep++
          self.player2.src('/static/video/excellent.mp4')
          self.player2.play()
        } else if (self.followRepeat === 1 && readscore < 90) {
          self.player2.src('/static/video/tryagain.mp4')
          self.player2.play()
        } else {
          self.scorelist.push({
            text: self.followText[self.followIndex],
            score: readscore
          })
          self.followRepeat = 0
          self.studystep++
          self.player2.src('/static/video/notcorrect.mp4')
          self.player2.play()
        }
      }

    }
  }
</script>

<style lang="scss" scoped>
  .el-row-item {
    margin: 20px 0;

    .title {
      font-size: 22px;
      font-weight: bolder;
      margin-bottom: 10px;
    }

    .desc {
      font-size: 15px;
      color: #999;
      line-height: 25px;
    }

    .btn {
      height: 16px;
      margin: 5px 0;
    }
  }

  .content {
    line-height: 25px;
    font-size: 15px;

    p {
      color: #666;
    }
  }

  .userscore {
    position: fixed;
    width: 280px;
    height: auto;
    left: 5px;
    bottom: 450px;
    z-index: 100;
    border: 0px solid #000000;

    .item {
      width: 100%;
      height: 30px;
      line-height: 30px;
      margin: 5px 5px 5px 5px;
    }
  }

  .vjs-icon-placeholder {
    display: none;
  }
</style>



