  const regNumber = /^\d+$/

  // 
  function validatorNumber (defautVal) {
    return {
        validator (val) {
          return regNumber.test(val)
        },
        default: defautVal
      }
  }

  // fontStyle
  function validatorFontStyle (...arg) {
    return {
        default () {
          return {
            text: arg[0],
            color: arg[1]
          }
        },
        validator (val) {
